$white: white;
.navbar{
    // border: 2px solid red;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    margin: 0;

            .Navbar-logo{
                // border: 2px solid red;
                margin-right: 30px;
            }
.Navbar-searchbar{
    // border: 2px solid red;
    display: flex;
    flex: 1;
    .Navbar-Input{
     width: 100%;
     padding: 5px 60px 5px 10px;
     outline: none;
     height: 42px;
     border-radius: 5px;
     color: #aaaaaa;
     font-size: 14px;
     font-weight: 700;
     background: rgba(228, 227, 227, 0.7);
     border: 2px solid rgba(228, 227, 227, 0.7);
     &::placeholder{
        color: #aaaaaa;
     }
    
    }
    .Navbar-button{
        position: relative;
        background: transparent;
        outline: none;
        border: none;
        font-size: 14px;
        right: 42px;
    }
}
.Navbar-nav{
    // border: 2px solid red;
    display: flex;
    justify-content: space-around;
    width: 300px;
    margin-right: 50px;
    text-align: center;
    .nav-box1{
        // border: 2px solid red;
      margin-left: 5px;
      margin-right: 5px;
      padding: 0px 8px;
      cursor: pointer;
      &:hover .nav-logo{
       color: orange;
       transition: 0.6s all ease-in-out;
      }
    
}
}
}
