$black: black;
$white: white;
$red : rgb(240, 76, 76);
.text-glow{
    color: $white;
    text-shadow: 0 0 4px #00b3ff,0 0 5px #00b3ff,0 0 6px #00b3ff,0 0 7px #00b3ff,0 0 8px #00b3ff;
        &:last-child span{
            color: white;
            text-shadow: 0 0 4px $white,0 0 5px $white,0 0 6px $white,0 0 7px $white,0 0 8px $white;
        }
    }
.text-orange{
    color: $white;
    text-shadow: 0 0 3px rgb(219, 152, 51),0 0 4px rgb(247, 136, 45),0 0 5px rgb(196, 63, 23),0 0 6px rgb(219, 152, 51),0 0 7px rgb(219, 152, 51);
    &:last-child span{
        color: white;
        text-shadow: 0 0 4px $red,0 0 5px $red,0 0 6px $red,0 0 7px $red,0 0 8px $red;
    }  
}
.alert{
    display: block;
    margin: 0;
    padding:0;
    border: none;
    border-radius: 0;
   background-color: $black;
   .alertion-text{
       .text-orange{
        p{
            @media screen and (min-width:320px) and (max-width:480px) {
               font-size: 12px;
               margin:0;
               padding: 0;
               margin-bottom: 6px;
           }
        }
    }
   }
}

