body{
  font-family: 'Baloo Bhai 2', cursive;
  overflow: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.text-glow{color:#fff;text-shadow:0 0 4px #00b3ff,0 0 5px #00b3ff,0 0 6px #00b3ff,0 0 7px #00b3ff,0 0 8px #00b3ff}.text-glow:last-child span{color:white;text-shadow:0 0 4px #fff,0 0 5px #fff,0 0 6px #fff,0 0 7px #fff,0 0 8px #fff}.text-orange{color:#fff;text-shadow:0 0 3px #db9833,0 0 4px #f7882d,0 0 5px #c43f17,0 0 6px #db9833,0 0 7px #db9833}.text-orange:last-child span{color:white;text-shadow:0 0 4px #f04c4c,0 0 5px #f04c4c,0 0 6px #f04c4c,0 0 7px #f04c4c,0 0 8px #f04c4c}.alert{display:block;margin:0;padding:0;border:none;border-radius:0;background-color:#000}@media screen and (min-width: 320px) and (max-width: 480px){.alert .alertion-text .text-orange p{font-size:12px;margin:0;padding:0;margin-bottom:6px}}

.active{background:-webkit-linear-gradient(101.19289deg, #4992f2 8.60417%, #4992f2 38.28125%, #00ede1 71.25579%, #00ede1 80.54861%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.logo-active{color:aqua}

.navbar{display:flex;justify-content:space-around;align-items:center;height:100px;margin:0}.navbar .Navbar-logo{margin-right:30px}.navbar .Navbar-searchbar{display:flex;flex:1 1}.navbar .Navbar-searchbar .Navbar-Input{width:100%;padding:5px 60px 5px 10px;outline:none;height:42px;border-radius:5px;color:#aaaaaa;font-size:14px;font-weight:700;background:rgba(228,227,227,0.7);border:2px solid rgba(228,227,227,0.7)}.navbar .Navbar-searchbar .Navbar-Input::-webkit-input-placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-Input:-ms-input-placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-Input::placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-button{position:relative;background:transparent;outline:none;border:none;font-size:14px;right:42px}.navbar .Navbar-nav{display:flex;justify-content:space-around;width:300px;margin-right:50px;text-align:center}.navbar .Navbar-nav .nav-box1{margin-left:5px;margin-right:5px;padding:0px 8px;cursor:pointer}.navbar .Navbar-nav .nav-box1:hover .nav-logo{color:orange;transition:0.6s all ease-in-out}

